@import 'src/style/variable.scss';

.banner {
  height: 100vh;
  position: relative;
  width: 100%;
  overflow: hidden;
  &__img {
    position: absolute;
    background-repeat: no-repeat;
    height: 100%;
    width: 100%;
    background-size: cover;
    background-position: center;
    animation: scaleBlock 10s ease 0s 1 both;
    left: 0;
    top: 0;
  }
  @keyframes scaleBlock {
    0% {
      transform: scale(1);
    }
    100% {
      transform: scale(1.2);
    }
  }

  &__ground {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    background-image: var(--homeBanner);
  }

  &__load {
    display: none;
    opacity: 0;
    visibility: hidden;
  }

  &.load {
    img {
      @extend %img;
    }

    .banner__img {
      width: 100%;
      height: 100%;
      overflow: hidden;
      display: flex;
      justify-content: center;
      align-items: center;
      z-index: 1;
    }
    .banner__load {
      display: block;
      opacity: 1;
      visibility: visible;
      z-index: 1;
      overflow: hidden;
      @extend %load;
    }
    .banner__ground {
      background-image: none;
      background: var(--gray);
    }
  }

  &__menu {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__body {
    z-index: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    margin-right: 10px;
  }

  &__title span {
    position: relative;
    transition: all 0.5s ease 0.3s;
    color: #fff !important;
    line-height: 110%;
    letter-spacing: 1px;
    text-align: center;
    width: 100%;
    font-size: calc(50px + (75 - 50) * ((100vw - 320px) / (1100 - 320)));
    user-select: none;
    text-align: left;
    display: flex;
    flex-direction: column;
    font-weight: bold;
    font-family: var(--fontTitle);
    text-transform: uppercase;
  }

  &__title span:nth-child(1),
  &__title span:nth-child(3) {
    right: -150%;
  }

  &__title span:nth-child(2) {
    display: block;
    left: -100%;
    color: transparent !important;
    -webkit-text-stroke: 1px #fff;
  }

  &__title.end span:nth-child(2) {
    left: 0%;
  }

  &__title.end span:nth-child(1),
  &__title.end span:nth-child(3) {
    right: 0%;
  }

  @media (max-width: $mw2 + px) {
    & {
      height: 100vh;
    }
  }
  @media (max-width: $mw3 + px) {
    & {
      height: 100vh;
    }
  }
  @media screen and (max-width: $mw4 + px) {
    & {
      height: 80vh;
    }
    &__ground {
      width: 100%;
      height: 100%;
    }

    &__title span,
    &__title {
      text-align: center;
    }
  }
  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    &__title span:nth-child(2) {
      color: #fff !important;
    }
  }
}
